<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">Instansi Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/settings/instansi'}" class="btn btn-primary text-white">Halaman Instansi</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul p-3">
            <CRow>
                <CCol sm="4">
                    <CInput label="Nama instansi" v-model.trim="$v.instansi.instansi.$model" :isValid="$v.instansi.instansi.$dirty ? !$v.instansi.instansi.$error : null" placeholder="Ketik Nama instansi" />
                </CCol>
                <CCol sm="3">
                    <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="instansi.active" v-model="instansi.active" />
                </CCol>
            </CRow>
            <CRow>
                <CCol col="12">
                    <CButton color="primary" class="text-white" @click="ToDatabase()">{{type}}</CButton>
                </CCol>
            </CRow>
        </div>
    </CCol>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            instansi : {
                instansi_id : null,
                instansi : null,
                active : true,
            },
            type: this.$route.params.type === 'edit' ? "update" : "insert",
        }
    },
    validations: {
        instansi: {
            instansi: {
                required
            },

        }
    },
    created() {
        this.getData()
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/instansi?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.instansi = e.data.data)

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/instansi?type=' + this.type, {
                        data: this.instansi
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: this.$route.params.type === 'edit' ? "Data Berhasil Di Ubah" : "Data Berhasil Di Tambahkan",
                        });

                        this.$router.push({
                            path: '/settings/instansi'
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
